import lazyLoading from './lazyLoading'

export default {
    name: 'ui',
    meta: {
        expanded: false,
        title: 'UI Elements',
        iconClass: 'vuestic-icon vuestic-icon-ui-elements'
    },
    children: [{
            name: 'Typography',
            path: '/ui/typography',
            component: lazyLoading('samples/ui/typography/Typography'),
            meta: {
                title: 'Typography'
            }
        },
        {
            name: 'Buttons',
            path: '/ui/buttons',
            component: lazyLoading('samples/ui/buttons/Buttons'),
            meta: {
                title: 'Buttons'
            }
        },
        {
            path: '/ui/icons',
            component: lazyLoading('samples/ui/icons/Icons'),
            meta: {
                title: 'Icons'
            },
            children: [{
                    path: '', // Default route
                    component: lazyLoading('samples/ui/icons/SetsList'),
                    meta: {
                        title: 'Icons'
                    }
                },
                {
                    path: ':name',
                    component: lazyLoading('samples/ui/icons/Set'),
                    props: true,
                    meta: {
                        title: 'Set'
                    }
                }
            ]
        },
        {
            name: 'Grid',
            path: '/ui/grid',
            component: lazyLoading('samples/ui/grid/Grid'),
            meta: {
                title: 'Grid'
            }
        },
        {
            name: 'Modals',
            path: '/ui/modals',
            component: lazyLoading('samples/ui/modals/Modals'),
            meta: {
                title: 'Modals'
            }
        }
    ]
}


