import Vue from 'vue';
import Axios from 'axios';

const verifySysadmin = async () => {
    let res = await Axios.get(`${window.adminUrl}/user/sysaccount`, {
        withCredentials: true
    });
    return res;
};

const isLoggedIn = () => {
    if (Vue.cookie.get('bzcms-admin-token')) {
        //TODO: verify SysAdmin
        return true;
    } else {
        return false;
    }
};

const logout = () => {
    "use strict";
    return Axios
        .get(`${window.apiUrl}/admin/logout`, {
            withCredentials: true
        })
        .then((res) => {
            if (res.status === 200) {
                return true;
            } else {
                return false;
            }
        })
}

export default {
    isLoggedIn,
    logout
}


