import lazyLoading from './lazyLoading.js'

export default {
    name: 'Posts',
    meta: {
        expanded: false,
        title: 'Posts',
        iconClass: 'glyphicon glyphicon-book'
    },
    children: [{
            name: 'new_post',
            path: '/post/create',
            component: lazyLoading('posts/Create'),
            meta: {
                title: 'New Post'
            }
        }, {
            name: 'posts',
            path: '/posts',
            component: lazyLoading('posts/Post'),
            meta: {
                title: 'List Posts'
            }
        },
        {
            name: 'category',
            path: '/category',
            component: lazyLoading('category/List'),
            meta: {
                title: 'Category'
            }
        }
    ]
}


