<template>
    <div class="table-responsive">
        <div class="d-flex flex-md-row flex-column justify-content-md-between align-items-center">
            <filter-bar @filter="onFilterSet"></filter-bar>
            <items-per-page :options="itemsPerPage"
                            :defaultPerPage="perPage"
                            @items-per-page="onItemsPerPage"></items-per-page>
        </div>
        <vuetable ref="vuetable"
                  :apiUrl="apiUrl"
                  :apiMode="apiMode"
                  :fields="tableFields"
                  :data="tableData"
                  :dataTotal="dataCount"
                  :dataManager="dataManager"
                  :css="css.table"
                  dataPath="data"
                  :paginationPath="paginationPath"
                  :appendParams="moreParams"
                  :perPage="perPage"
                  :httpOptions="httpOptions"
                  @vuetable:pagination-data="onPaginationData">

            <template slot="actions" scope="props">
                <div class="table-button-container">
                    <button class="btn btn-primary btn-micro" @click="editRow(props)"><i class="fa fa-edit"></i> Edit</button>&nbsp;&nbsp;
                    <button class="btn btn-danger btn-micro" @click="deleteRow(props)"><i class="fa fa-remove"></i> Delete</button>&nbsp;&nbsp;
                </div>
            </template>

        </vuetable>
        <div class="d-flex justify-content-center mb-4">
            <vuetable-pagination ref="pagination"
                                 :css="css.pagination"
                                 :onEachSide="onEachSide"
                                 @vuetable-pagination:change-page="onChangePage">
            </vuetable-pagination>
        </div>
        <modal :show.sync="show" ref="confirmModal" @ok="confirmDelete">
            <div slot="title">Confirm</div>
            <div> Do you want to remove this item?</div>
        </modal>
    </div>
</template>

<script>
    import Vuetable from 'vuetable-2/src/components/Vuetable'
    import VuetablePagination from 'vuetable-2/src/components/VuetablePagination'
    import FilterBar from './datatable-components/FilterBar.vue'
    import ItemsPerPage from './datatable-components/ItemsPerPage.vue'
    import Vue from 'vue'
    import moment from  'moment';
    import LocalData from './data/local-data'
    import DataTableStyles from '../../vuestic-components/vuestic-datatable/data/data-table-styles'
    import Modal from '../../vuestic-components/vuestic-modal/VuesticModal'
    const originalData = LocalData.data

    export default {
        name: 'vuestic-data-table',
        components: {
            Modal,
            FilterBar,
            Vuetable,
            VuetablePagination,
            ItemsPerPage
        },
        watch: {
            reloadTable(){
                Vue.nextTick(() => this.$refs.vuetable.refresh());
            }
        },
        props: {
            apiUrl: {
                type: String
            },
            tableFields: {
                type: Array,
                required: true
            },
            itemsPerPage: {
                type: Array,
                required: true
            },
            onEachSide: {
                type: Number,
                default () {
                    return 2
                }
            },
            reloadTable: {
                type: Boolean,
                default () {
                    return false
                }
            },
            apiMode: {
                type: Boolean,
                default () {
                    return true
                }
            },
            data: {
                type: Array
            },
            httpOptions: {
                type: Object,
                default () {
                    return {}
                }
            },
            sortFunctions: {
                type: Object
            },
            paginationPath: {
                type: String,
                default () {
                    return ''
                }
            }
        },
        data () {
            return {
                selectItem: {},
                show: true,
                tableData: LocalData,
                perPage: 10,
                colorClasses: {},
                moreParams: {},
                dataCount: 0,
                css: DataTableStyles
            }
        },
        methods: {
            image(value){
                return `<img style="max-width: 200px" src="${window.webUrl}${value}">`;
            },
            formatDate(value, fmt) {
                if (value == null) {
                    return '';
                }
                fmt = (typeof fmt == 'undefined') ? 'D MMM YYYY' : fmt;
                return moment(value, 'YYYY-MM-DD').format(fmt)
            },
            status(value){
                return value ? "<span class='badge badge-pill badge-info'>Publish</span>" : "<span class='badge badge-pill badge-danger'>Unpublish</span>";
            },
            confirmDelete(){
                this.$emit('deleteRow', this.selectItem);
                this.selectItem = [];
            },
            editRow(props){
                this.$emit('gotoDetail', props.rowData);
            },
            deleteRow(props){
                this.$refs.confirmModal.open();
                this.selectItem = props.rowData;
            },
            onFilterSet (filterText) {
                if (this.apiMode) {
                    this.moreParams = {
                        'filter': filterText
                    }
                } else {
                    const txt = new RegExp(filterText, 'i');
                    this.tableData.data = originalData.filter(function (item) {
                        return item.name.search(txt) >= 0 || item.email.search(txt) >= 0
                    })
                }
                Vue.nextTick(() => this.$refs.vuetable.refresh())
            },
            onItemsPerPage (itemsPerPageValue) {
                this.perPage = itemsPerPageValue;
                Vue.nextTick(() => this.$refs.vuetable.refresh())
            },
            onPaginationData (paginationData) {
                this.$refs.pagination.setPaginationData(paginationData)
            },
            onChangePage (page) {
                this.$refs.vuetable.changePage(page)
            },
            dataManager (sortOrder, pagination) {
                let data = this.tableData.data;
                let sortFunctions = this.sortFunctions;

                if (sortOrder.length > 0) {
                    data.sort(function (item1, item2) {
                        const sortField = sortOrder[0].sortField;
                        let fn = sortFunctions[sortField];
                        if (fn) {
                            return fn(item1[sortField], item2[sortField])
                        }
                    });

                    if (sortOrder[0].direction === 'desc') {
                        data.reverse()
                    }
                }

                pagination = this.$refs.vuetable.makePagination(data.length)

                return {
                    pagination: pagination,
                    data: data.slice(pagination.from - 1, pagination.to)
                }
            }
        }
    }
</script>

<style lang="scss">
    @import "../../../sass/variables";

    @media (max-width: 1258px) {
        .pagination-link-btn:first-child, .pagination-link-btn:last-child {
            display: none;
        }

        .pagination-link-btn:nth-child(2) {
            border-top-left-radius: $btn-border-radius !important;
            border-bottom-left-radius: $btn-border-radius !important;
        }

        .pagination-link-btn:nth-last-child(2) {
            border-top-right-radius: $btn-border-radius !important;
            border-bottom-right-radius: $btn-border-radius !important;
        }
    }

    @media (max-width: 576px) {
        .hide-not-focused-btn:not(.focus) {
            display: none;
        }
    }
</style>



